<template>
  <modal-el
  v-if="open" v-model="open"
  xs
  >
    <div class="uppercase font-bold"> </div>

    <form
    @submit.prevent="onSubmit"
    class="order-form"
    ref="order"
    >
      <!--    -->
      <div
      v-if="!order.status"
      class="order-form__form"
      >
        <div class="my-5 text-lg font-semibold text-indigo-800"
        data-aos="fade-up" data-aos-duration="800"
        >
              &nbsp;   &nbsp; 
        </div>

        <textInput
        v-model="phone"
        type="tel"
        class="text-2xl w-full my-5"
        maska="+7(###)###-####"
        pattern="\+7\(\d{3}\)\d{3}-\d{4}"
        label="  "
        focus
        data-aos="fade-up" data-aos-duration="800" data-aos-delay="200"
        />

        <buttonEl lg :disabled="!valid" class="w-full mb-3"></buttonEl>
      </div>

      <!--   -->
      <div
      v-if="order.status === 'sending'"
      class="order-form__error-view
      text-center"
      >
        <ArrowPathIcon class="icon order-form__big-icon text-sky-500 my-5 rotate"
        data-aos="zoom-in-up" data-aos-duration="800" data-aos-delay="100"
        />

        <h2 class="text-2xl font-bold mb-5 text-sky-600"
        data-aos="fade-up" data-aos-duration="800" data-aos-delay="300"
        >...</h2>
      </div>

      <!--  -->
      <div
      v-if="order.status === 'success'"
      class="order-form__error-view
      text-center"
      >
        <CheckIcon class="icon order-form__big-icon text-sky-500 my-5"
        data-aos="zoom-in-up" data-aos-duration="800" data-aos-delay="300"
        />

        <h2 class="text-2xl font-bold mb-2 text-sky-700">  </h2>

        <p class="mb-4">      </p>

        <buttonEl lg @click.prevent="open = false" class="w-full mb-3"></buttonEl>

      </div>

      <!--  -->
      <div
      v-if="order.status === 'error'"
      class="order-form__error-view
      text-center"
      >
        <icon-el icon="x-mark" class="order-form__big-icon text-red-700 my-5"/>

        <h2 class="text-2xl font-bold mb-2 text-red-900"> </h2>

        <p class="mb-4"> ,   </p>

        <button
        @click.prevent="formAgain"
        class="px-4 mb-3 py-2 font-semibold text-sm text-white rounded-xl w-full text-xl bg-red-900 hover:bg-red-600 transition-colors"
        >  </button>
      </div>

    </form>
  </modal-el>
</template>

<script>
import { mapGetters } from 'vuex'
import textInput from '@/components/core/inputs/inputText.vue'
import { vMaska } from "maska" //  
import { ArrowPathIcon, CheckIcon } from '@heroicons/vue/24/solid'

export default {
  data() {
    return {
      phone: '+7',
    }
  },

  methods: {
    onSubmit() {
      this.$store.dispatch('sendData', this.phone)
    },

    formAgain() {
      this.$store.dispatch('refreshForm')
    },
  },

  directives: { maska: vMaska }, //  

  components: {
    textInput,
    ArrowPathIcon,
    CheckIcon,
  },

  computed: {
    ...mapGetters([
      'order'
    ]),

    open: {
      get() {
        return this.order.modalOpen
      },
      set(value) {
        this.$store.dispatch('openOrderModal', value)
      }
    },

    valid() {
      let number = this.phone.match(/[0-9]/g)
      if (number) return (number.length === 11 && (number[0] == 7 || number[0] == 8))
      return false
    },
  },

}
</script>

<style lang="scss">
.order-form {
  position: relative;

  // -
  & {
    .order-form__form {
      visibility: visible;
    }

    .order-form__error-view {
    }

    .order-form__big-icon {
      font-size: 5rem;
    }
  }

  // 
  &.order-form_sending {

    .order-form__form {
      visibility: hidden;
    }
  }

  // 
  &.order-form_success {

    .order-form__form {
      visibility: hidden;
    }
  }

  // 
  &.order-form_error {

    .order-form__form {
      visibility: hidden;
    }
  }
}
</style>
